import { Button, IconButton, Typography } from "@mui/material";
import { Stack } from "@mui/system";
import React from "react";
import Navbar from "../../components/navbar/Navbar";
import "./profile.css";
import InputLabel from "@mui/material/InputLabel";
import MenuItem from "@mui/material/MenuItem";
import FormControl from "@mui/material/FormControl";
import Select from "@mui/material/Select";
import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";
import Paper from "@mui/material/Paper";
import EditOutlinedIcon from "@mui/icons-material/EditOutlined";
import DeleteOutlinedIcon from "@mui/icons-material/DeleteOutlined";
import AddOutlinedIcon from "@mui/icons-material/AddOutlined";

const Profile = () => {
  const [age, setAge] = React.useState("");

  const handleChange = (event) => {
    setAge(event.target.value);
  };

  return (
    <div>
      <Navbar />
      <div className="container">
        <Stack
          direction={"row"}
          alignItems={"center"}
          justifyContent={"space-between"}
        >
          <Typography id="title_name">Profiles</Typography>
          <Button
            startIcon={<AddOutlinedIcon />}
            id="add_button"
            variant="outlined"
          >
            Add profile
          </Button>
        </Stack>
        <Stack mt={3}>
          <FormControl id="select">
            <InputLabel id="demo-simple-select-label">
              Select category
            </InputLabel>
            <Select
              labelId="demo-simple-select-label"
              id="demo-simple-select"
              value={age}
              label="Select category"
              onChange={handleChange}
            >
              <MenuItem value={30}>Thirty</MenuItem>
            </Select>
          </FormControl>
        </Stack>
        <Stack mt={2}>
          <TableContainer component={Paper}>
            <Table sx={{ minWidth: 650 }} aria-label="simple table">
              <TableHead>
                <TableRow
                  style={{ background: "#31456A", fontFamily: "roboto" }}
                >
                  <TableCell style={{ color: "#fff" }}>ID</TableCell>
                  <TableCell
                    align="left"
                    style={{
                      color: "#fff",
                      fontFamily: "roboto",
                      fontSize: "17px",
                    }}
                  >
                    Name
                  </TableCell>
                  <TableCell
                    align="left"
                    style={{
                      color: "#fff",
                      fontFamily: "roboto",
                      fontSize: "17px",
                    }}
                  >
                    Category
                  </TableCell>
                  <TableCell
                    align="left"
                    style={{
                      color: "#fff",
                      fontFamily: "roboto",
                      fontSize: "17px",
                    }}
                  >
                    VIP status
                  </TableCell>
                  <TableCell
                    align="left"
                    style={{
                      color: "#fff",
                      fontFamily: "roboto",
                      fontSize: "17px",
                    }}
                  >
                    Status
                  </TableCell>
                  <TableCell
                    align="left"
                    style={{
                      color: "#fff",
                      fontFamily: "roboto",
                      fontSize: "17px",
                    }}
                  >
                    View count
                  </TableCell>
                  <TableCell
                    align="left"
                    style={{
                      color: "#fff",
                      fontFamily: "roboto",
                      fontSize: "17px",
                    }}
                  >
                    Like
                  </TableCell>
                  <TableCell
                    align="left"
                    style={{
                      color: "#fff",
                      fontFamily: "roboto",
                      fontSize: "17px",
                    }}
                  >
                    Dislike
                  </TableCell>
                  <TableCell
                    align="left"
                    style={{
                      color: "#fff",
                      fontFamily: "roboto",
                      fontSize: "17px",
                    }}
                  >
                    Delete
                  </TableCell>
                  <TableCell
                    align="left"
                    style={{
                      color: "#fff",
                      fontFamily: "roboto",
                      fontSize: "17px",
                    }}
                  >
                    Edit
                  </TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                <TableRow>
                  <TableCell style={{ color: "#31456A" }}>1</TableCell>
                  <TableCell align="left" style={{ color: "#31456A" }}>
                    Kinoteatr Berkarar
                  </TableCell>
                  <TableCell align="left" style={{ color: "#31456A" }}>
                    Movie
                  </TableCell>
                  <TableCell align="left" style={{ color: "#31456A" }}>
                    on
                  </TableCell>
                  <TableCell align="left" style={{ color: "#31456A" }}>
                    off
                  </TableCell>
                  <TableCell align="left" style={{ color: "#31456A" }}>
                    777
                  </TableCell>
                  <TableCell align="left" style={{ color: "#31456A" }}>
                    765
                  </TableCell>
                  <TableCell align="left" style={{ color: "#31456A" }}>
                    675
                  </TableCell>
                  <TableCell align="left">
                    <IconButton style={{ color: "#CD2791" }}>
                      <DeleteOutlinedIcon />
                    </IconButton>
                  </TableCell>
                  <TableCell align="left">
                    <IconButton style={{ color: "#F59300" }}>
                      <EditOutlinedIcon />
                    </IconButton>
                  </TableCell>
                </TableRow>
              </TableBody>
            </Table>
          </TableContainer>
        </Stack>
      </div>
    </div>
  );
};

export default Profile;
